.parent{
    width: 35%;
    height: 350px;
    margin: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}

.container{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    width: 100%;
    height: 100%;
    background-color: rgb(11 35 70 / 62%);
    border-radius: 15px;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
    color: white;
}

.questionText{
    width: 50%;
    height: 100%;
    display: flex;
    padding-top: 54px;
    padding-left: 50px;
    font-size: 25px;
    justify-content: flex-start;
    flex-direction: column;
    font-family: sans-serif;
}

.options{
    width: 50%;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-evenly;
    height: 100%;
    font-size: 20px;
}

.options span{
    padding: 15px;
    height: 30px;
    font-size: 17px;
    width: 70%;
    border: solid 3px #ffffff7d;;
    border-radius: 10px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-left: 10px;
    cursor: pointer;
    transition: 10ms;
}

.options span:hover{
   background-color:  #1126415e;
}

.score{
    position: absolute;
    font-size: 30px;
    font-weight: bold;
    font-family: monospace;
}